'use client'

import { useState, useEffect } from 'react'
import DashboardLayout from '@/components/layout/DashboardLayout'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Badge } from '@/components/ui/badge'
import { User } from '@/lib/auth'
import { mockStudents, mockThesisScores, mockThesisDrafts } from '@/lib/mockData'

export default function SuperadminDashboard() {
  const [user, setUser] = useState<User | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    // Get user from session - in production, verify with API
    const mockUser: User = {
      id: 1,
      username: 'superadmin',
      email: 'admin@university.edu',
      role: 'superadmin',
      full_name: 'Super Administrator'
    }
    setUser(mockUser)
    setLoading(false)
  }, [])

  const handleExportPDF = async () => {
    try {
      const response = await fetch('/api/report/pdf')
      if (response.ok) {
        const blob = await response.blob()
        const url = window.URL.createObjectURL(blob)
        const a = document.createElement('a')
        a.href = url
        a.download = 'laporan-nilai-tesis.pdf'
        document.body.appendChild(a)
        a.click()
        window.URL.revokeObjectURL(url)
        document.body.removeChild(a)
      }
    } catch (error) {
      console.error('Error exporting PDF:', error)
    }
  }

  if (loading || !user) {
    return <div className="min-h-screen flex items-center justify-center">Loading...</div>
  }

  const studentsWithScores = mockStudents.map(student => {
    const score = mockThesisScores.find(s => s.student_id === student.id)
    const draft = mockThesisDrafts.find(d => d.student_id === student.id)
    return { ...student, score, draft }
  })

  const totalStudents = mockStudents.length
  const completedScores = mockThesisScores.filter(s => s.final_score !== undefined).length
  const uploadedDrafts = mockThesisDrafts.length

  return (
    <DashboardLayout user={user}>
      <div className="space-y-6">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Dashboard Superadmin</h1>
          <p className="text-gray-600">Kelola seluruh data tesis dan nilai mahasiswa</p>
        </div>

        {/* Statistics Cards */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Total Mahasiswa</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{totalStudents}</div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Draft Terupload</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{uploadedDrafts}</div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Nilai Selesai</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{completedScores}</div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Progress</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                {totalStudents > 0 ? Math.round((completedScores / totalStudents) * 100) : 0}%
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Export Button */}
        <div className="flex justify-end">
          <Button onClick={handleExportPDF} className="bg-blue-600 hover:bg-blue-700">
            Export Laporan PDF
          </Button>
        </div>

        {/* Students Table */}
        <Card>
          <CardHeader>
            <CardTitle>Data Mahasiswa dan Nilai</CardTitle>
            <CardDescription>
              Daftar seluruh mahasiswa dengan status tesis dan nilai
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>NIM</TableHead>
                  <TableHead>Nama</TableHead>
                  <TableHead>Judul Tesis</TableHead>
                  <TableHead>Draft</TableHead>
                  <TableHead>Nilai Pembimbing</TableHead>
                  <TableHead>Nilai Penguji</TableHead>
                  <TableHead>Nilai Akhir</TableHead>
                  <TableHead>Status</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {studentsWithScores.map((student) => (
                  <TableRow key={student.id}>
                    <TableCell className="font-medium">{student.nim}</TableCell>
                    <TableCell>{student.name}</TableCell>
                    <TableCell className="max-w-xs truncate">{student.thesis_title}</TableCell>
                    <TableCell>
                      {student.draft ? (
                        <Badge variant="secondary">Terupload</Badge>
                      ) : (
                        <Badge variant="outline">Belum Upload</Badge>
                      )}
                    </TableCell>
                    <TableCell>
                      {student.score?.dosbim_score ? (
                        <span className="font-medium">{student.score.dosbim_score}</span>
                      ) : (
                        <span className="text-gray-400">-</span>
                      )}
                    </TableCell>
                    <TableCell>
                      {student.score?.dospenguji_score ? (
                        <span className="font-medium">{student.score.dospenguji_score}</span>
                      ) : (
                        <span className="text-gray-400">-</span>
                      )}
                    </TableCell>
                    <TableCell>
                      {student.score?.final_score ? (
                        <span className="font-bold text-green-600">{student.score.final_score}</span>
                      ) : (
                        <span className="text-gray-400">-</span>
                      )}
                    </TableCell>
                    <TableCell>
                      {student.score?.final_score ? (
                        <Badge className="bg-green-100 text-green-800">Selesai</Badge>
                      ) : (
                        <Badge variant="outline">Proses</Badge>
                      )}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      </div>
    </DashboardLayout>
  )
}
